@use '../foundation/_variable' as *;

.l-footer {
    background-color: $color-base;
    c-inner {
        padding-top: 3.6rem;
        text-align: center;
        font-weight: bold;
    }
    &__nav ul {
        display: flex;
        gap: 2.9rem;
        justify-content: right;
        margin-bottom: 2rem;
        li {
            text-align: left;
        }
    }
    small {
        font-weight: normal;
        font-size: 1.2rem;
        text-align: center;
    }
}