  object-fit: cover;
}
.p-works__cardText {
  padding: 1.8rem 1.8rem 0;
}
.p-works__cardHeading {
  margin-bottom: 3rem;
  line-height: 1.5;
}
.p-works__cardDetail {
  font-size: 1.4rem;
  line-height: 1.43;
  margin-bottom: 4rem;
}

.p-faq {
  padding-top: 5.5rem;
  padding-bottom: 8.6rem;
}
.p-faq .c-sectionTitle {
  margin-bottom: 3.2rem;
}
.p-faq__table {
  font-weight: bold;
  font-size: 1.4rem;
  line-height: 1.65;
  max-width: 90.7rem;
  margin: 0 auto;
}
.p-faq__table dt, .p-faq__table dd {
  border: 1px solid gray;
}
.p-faq__table dt {
  text-align: center;
  height: 5.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.6rem;
  user-select: none;
  cursor: pointer;
}
@media (max-width: 576px) {
  .p-faq__table dt {
    min-height: 5.8rem;
    height: auto;
  }
}
.p-faq__table dd {
  background-color: darkgray;
  color: #663300;
  visibility: hidden;
  max-height: 0;
  border: none;
}

.p-faq .js-accordion__active {
  visibility: inherit;
  max-height: 50rem;
  transition: visibility 0.3s, max-height 0.3s;
  padding: 2.3rem 8.7rem 2.3rem 3.5rem;
}
@media (max-width: 992px) {
  .p-faq .js-accordion__active {
    padding: 2.3rem 3.5rem;
  }
}
@media (max-width: 576px) {
  .p-faq .js-accordion__active {
    padding: 2.3rem 2.4rem;
  }
}

.js-scroll__target {
  opacity: 0;
  transform: translate3d(0, 30px, 0);
}

.js-scroll__target.is-active {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition: 1s;
}

