@use '../../foundation/_variable' as *;

.c-sectionTitle {
        margin: 0 auto;
    &__title {
        font-size: 3.2rem;
        line-height: 1.44;
        font-weight: bold;
        margin-bottom: 1rem;
        text-align: center;
        @include breakpoint(large) {
            font-size: 2.4rem;
        }
    }
    &__en {
        font-size: 1.6rem;
        color: $color-orange;
        font-weight: bold;
    }
}