@use '../../foundation/_variable' as *;

.p-lineup {
    background-color: $color-bg-cream;
    padding-top: 3rem;
    &__gridWrapper {
        margin-top: 4rem;
        display: grid;
        grid-template-columns: 19rem 19rem 19rem;
        grid-template-rows: 19rem 19rem 19rem 19rem 19rem;
        gap: 1.3rem;
    }
    &__gridItem {
        position: relative;
    }
    &__name {
        font-size: 2.2rem;
        color: $color-base;
        font-weight: bold;
        position: absolute;
        left: 0;
        bottom: 0;
    }
}